import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ChatClientComponent } from './components/chat-client/chat-client.component';
import { ChatClientHomeComponent } from './components/chat-client-home/chat-client-home.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: 'home',
    component: ChatClientHomeComponent
  },
  {
    path: 'chat',
    component: ChatClientComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ClientRoutingModule { }
