import { Component, OnInit, ViewChild, ElementRef, AfterViewChecked, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import {
  IChatMessage,
  SenderType,
  RoomStatus,
  LocalChatMessage
} from '../../../shared/interfaces/chat-talk.interface';
import { ChatBotService, IBotResponse } from '../../../shared/services/chatbot.service';
import { ChatTalkApiService } from '../../../shared/services/chat-talk-api.service';
import { ChatTalkSocketService } from '../../../shared/services/chat-talk-socket.service';

@Component({
  selector: 'app-chat-client',
  standalone: false,
  templateUrl: './chat-client.component.html',
  styleUrls: ['./chat-client.component.scss']
})
export class ChatClientComponent implements OnInit, AfterViewChecked, OnDestroy {
  @ViewChild('chatMessages') private chatContainer!: ElementRef;

  storeId: string = '';
  storeName: string = '';
  roomId: string | null = null;
  clientId: string | null = null;
  organizationId: string = '1'; // TODO: 실제 organization ID 가져오기
  isNewChat: boolean = false;
  
  messages: LocalChatMessage[] = [];
  newMessage: string = '';
  
  welcomeMessage: string = '';
  showQuickReplies: boolean = true;
  quickReplies: string[] = [];
  
  isLoading: boolean = false;
  isConnecting: boolean = false;
  connectionError: string | null = null;
  currentTime: Date = new Date();
  
  private destroy$ = new Subject<void>();

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private chatBotService: ChatBotService,
    private apiService: ChatTalkApiService,
    private socketService: ChatTalkSocketService
  ) {}

  async ngOnInit(): Promise<void> {
    // URL 파라미터 처리
    this.route.queryParams.pipe(takeUntil(this.destroy$)).subscribe(async params => {
      this.storeId = params['storeId'] || '';
      this.storeName = params['storeName'] || '스토어';
      this.roomId = params['roomId'] || null;
      this.isNewChat = params['isNew'] === 'true';
      
      if (this.isNewChat) {
        await this.initNewChat();
      } else if (this.roomId) {
        this.loadExistingChat();
      }
    });

    // 빠른 답장 로드
    this.quickReplies = this.chatBotService.getQuickReplies();

    // WebSocket 연결
    await this.connectWebSocket();
  }

  ngAfterViewChecked(): void {
    this.scrollToBottom();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * 새 채팅 초기화
   */
  async initNewChat(): Promise<void> {
    this.isConnecting = true;
    this.connectionError = null;


    try {
      // 1. 고객 식별 (핑거프린트 기반)
      const fingerprintData = this.generateFingerprint();
      
      
      // 백엔드 DTO 형식에 맞게 요청
      const identifyRequest = {
        fingerprintData: fingerprintData,
        pluginKey: this.organizationId // organizationId를 pluginKey로 사용
      };
      
      
      const identifyResponse = await this.apiService.identifyCustomerWithObject(
        identifyRequest
      ).toPromise();

      if (!identifyResponse || !identifyResponse.client) {
        throw new Error('고객 식별 API 응답이 없습니다');
      }

      this.clientId = identifyResponse.client.id;

      // 2. 채팅방 생성
      
      // 백엔드 DTO 형식에 맞게 요청 (organizationId 사용)
      const createRoomRequest = {
        clientId: this.clientId,
        organizationId: this.organizationId
      };
      

      const roomResponse = await this.apiService.createRoomWithOrganization(
        createRoomRequest
      ).toPromise();

      if (!roomResponse) {
        throw new Error('채팅방 생성 API 응답이 없습니다');
      }

      this.roomId = roomResponse.id;

      // 3. 환영 메시지 표시
      this.welcomeMessage = `${this.storeName}에 오신 것을 환영합니다! 무엇을 도와드릴까요?`;
      this.currentTime = new Date();
      this.showQuickReplies = true;

      const welcomeMsg: LocalChatMessage = {
        id: 'welcome',
        roomId: this.roomId || '',
        senderType: SenderType.BOT,
        content: this.welcomeMessage,
        createdAt: new Date()
      };
      this.messages = [welcomeMsg];

      // 4. WebSocket 방 참가
      await this.joinRoom();

      this.isConnecting = false;

    } catch (error: any) {
      console.warn('❌ 에러 상세:', {
        message: error.message,
        status: error.status,
        error: error.error,
        stack: error.stack
      });
      
      this.connectionError = '채팅을 시작할 수 없습니다. 잠시 후 다시 시도해주세요.';
      
      // 구체적인 에러 메시지 제공
      if (error.status === 404) {
        this.connectionError = '채팅 서비스를 찾을 수 없습니다. 관리자에게 문의해주세요.';
      } else if (error.status === 500) {
        this.connectionError = '서버 오류가 발생했습니다. 잠시 후 다시 시도해주세요.';
      } else if (error.status === 0) {
        this.connectionError = '서버에 연결할 수 없습니다. 네트워크 연결을 확인해주세요.';
      } else if (error.error?.message) {
        this.connectionError = `오류: ${error.error.message}`;
      }
      
      this.isConnecting = false;
    }
  }

  /**
   * 브라우저 핑거프린트 생성
   */
  private generateFingerprint(): any {
    const nav = window.navigator;
    const screen = window.screen;

    return {
      userAgent: nav.userAgent,
      language: nav.language,
      platform: nav.platform,
      screen: `${screen.width}x${screen.height}`,
      timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
      canvas: this.generateCanvasFingerprint(),
      memory: (nav as any).deviceMemory || 'unknown',
      cores: nav.hardwareConcurrency || 'unknown',
      cookieEnabled: nav.cookieEnabled,
      doNotTrack: (nav as any).doNotTrack || 'unspecified',
      timestamp: Date.now()
    };
  }

  /**
   * Canvas 핑거프린트 생성
   */
  private generateCanvasFingerprint(): string {
    try {
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      if (!ctx) return 'no-canvas';

      canvas.width = 200;
      canvas.height = 50;
      ctx.textBaseline = 'top';
      ctx.font = '14px Arial';
      ctx.fillText('Browser Fingerprint', 10, 10);

      return canvas.toDataURL().substring(0, 100);
    } catch (e) {
      return 'canvas-error';
    }
  }

  /**
   * WebSocket 연결
   */
  private async connectWebSocket(): Promise<void> {
    try {
      await this.socketService.connect();

      // 새 메시지 이벤트 구독
      this.socketService.onEvent('newMessage').pipe(takeUntil(this.destroy$))
        .subscribe((data: any) => {
          if (data.roomId === this.roomId) {
            this.handleNewMessage(data.message);
          }
        });

    } catch (error) {
    }
  }

  /**
   * 방 참가
   */
  private async joinRoom(): Promise<void> {
    if (!this.roomId || !this.clientId) return;

    this.socketService.emitEvent('joinRoom', {
      roomId: this.roomId,
      clientId: this.clientId
    });

  }

  /**
   * 새 메시지 처리
   */
  private handleNewMessage(message: IChatMessage): void {
    const localMessage: LocalChatMessage = {
      ...message,
      status: 'sent'
    };
    this.messages.push(localMessage);
  }

  /**
   * 기존 채팅 로드
   */
  loadExistingChat(): void {
    this.isLoading = true;
    // TODO: API 호출로 기존 메시지 로드
    this.isLoading = false;
  }

  /**
   * 메시지 전송
   */
  async sendMessage(event?: Event): Promise<void> {
    if (event) {
      event.preventDefault();
    }

    const content = this.newMessage.trim();
    if (!content || !this.roomId) return;

    // 사용자 메시지 추가 (로컬)
    const userMessage: LocalChatMessage = {
      id: `temp-${Date.now()}`,
      roomId: this.roomId,
      senderType: SenderType.CLIENT,
      content: content,
      createdAt: new Date(),
      status: 'pending'
    };
    
    this.messages.push(userMessage);
    this.newMessage = '';

    // 실제 메시지 전송 (WebSocket)
    try {
      this.socketService.emitEvent('sendMessage', {
        roomId: this.roomId,
        content: content,
        senderType: 'client'
      });

      // 메시지 상태 업데이트
      userMessage.status = 'sent';

      // 챗봇 모드인 경우 자동 응답
      if (this.showQuickReplies) {
        await this.processBotResponse(content);
      }

    } catch (error) {
      userMessage.status = 'failed';
    }
  }

  /**
   * 챗봇 응답 처리
   */
  private async processBotResponse(userMessage: string): Promise<void> {
    // 타이핑 인디케이터 표시
    setTimeout(async () => {
      const response: IBotResponse = await this.chatBotService.processMessage(userMessage);
      
      // 봇 응답 메시지 추가
      const botMessage: LocalChatMessage = {
        id: `bot-${Date.now()}`,
        roomId: this.roomId || '',
        senderType: SenderType.BOT,
        content: response.message,
        createdAt: new Date()
      };
      
      this.messages.push(botMessage);

      // 액션 처리
      if (response.action === 'connectAgent') {
        this.connectToAgent();
      }

      // 빠른 답장 업데이트
      if (response.quickReplies) {
        this.quickReplies = response.quickReplies;
      }
    }, 1000); // 1초 지연으로 자연스러운 응답
  }

  /**
   * 빠른 답장 선택
   */
  selectQuickReply(reply: string): void {
    this.newMessage = reply;
    this.sendMessage();
  }

  /**
   * 상담원 연결
   */
  private connectToAgent(): void {
    
    if (!this.roomId) {
      return;
    }

    // 상담원 연결 요청 전송
    this.socketService.emitEvent('requestSupport', {
      roomId: this.roomId
    });

    // 빠른 답장 숨기기
    this.showQuickReplies = false;
    
    // 시스템 메시지 추가
    const systemMessage: LocalChatMessage = {
      id: `system-${Date.now()}`,
      roomId: this.roomId,
      senderType: SenderType.SYSTEM,
      content: '상담원에게 연결 중입니다. 잠시만 기다려주세요...',
      createdAt: new Date()
    };
    this.messages.push(systemMessage);
  }

  /**
  /**
   * 뒤로 가기
   */
  goBack(): void {
    this.router.navigate(['/chat-talk/client/home'], {
      queryParams: { storeId: this.storeId }
    });
  }

  /**
   * 상담 종료 및 새로 시작
   */
  resetChat(): void {
    if (!confirm('현재 상담을 종료하고 새로 시작하시겠습니까?')) {
      return;
    }

    // 소켓 연결 해제
    if (this.roomId) {
      this.socketService.emitEvent('leaveRoom', { roomId: this.roomId });
    }

    // 상태 초기화
    this.messages = [];
    this.roomId = null;
    this.clientId = null;
    this.showQuickReplies = true;
    this.isConnecting = false;
    this.connectionError = null;

    // 새 채팅 시작
    this.initNewChat();
  }

  /**
   * 연결 취소
   */
  cancelConnection(): void {
    this.isConnecting = false;
    this.connectionError = null;
    
    // 홈으로 돌아가기
    this.goBack();
  }

  /**
   * 입력 변경 처리
   */
  onInputChange(): void {
    const textarea = document.querySelector('textarea') as HTMLTextAreaElement;
    if (textarea) {
      this.autoResizeTextarea(textarea);
    }
  }

  /**
   * Enter 키 처리
   */
  onEnterPress(event: KeyboardEvent): void {
    if (!event.shiftKey) {
      event.preventDefault();
      this.sendMessage();
    }
  }

  /**
   * 텍스트 영역 자동 크기 조정
   */
  private autoResizeTextarea(textarea: HTMLTextAreaElement): void {
    textarea.style.height = 'auto';
    textarea.style.height = Math.min(textarea.scrollHeight, 120) + 'px';
  }

  /**
   * 스크롤 하단으로 이동
   */
  private scrollToBottom(): void {
    try {
      if (this.chatContainer) {
        this.chatContainer.nativeElement.scrollTop = 
          this.chatContainer.nativeElement.scrollHeight;
      }
    } catch (err) {
    }
  }

  /**
   * 시간 포맷
   */
  formatTime(date: Date): string {
    return new Date(date).toLocaleTimeString('ko-KR', {
      hour: '2-digit',
      minute: '2-digit'
    });
  }
}
