import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { IChatRoom, RoomStatus } from '../../../shared/interfaces/chat-talk.interface';

@Component({
  selector: 'app-chat-client-home',
  standalone: false,
  templateUrl: './chat-client-home.component.html',
  styleUrls: ['./chat-client-home.component.scss']
})
export class ChatClientHomeComponent implements OnInit, OnDestroy {
  storeId: string = '';
  chatRooms: IChatRoom[] = [];
  isLoading: boolean = false;
  
  private destroy$ = new Subject<void>();

  constructor(
    private router: Router,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    // URL 파라미터에서 storeId 가져오기
    this.route.queryParams.pipe(takeUntil(this.destroy$)).subscribe(params => {
      this.storeId = params['storeId'] || '';
      if (this.storeId) {
        this.loadChatRooms();
      }
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * 채팅방 목록 로드
   */
  loadChatRooms(): void {
    this.isLoading = true;
    // TODO: API 호출로 채팅방 목록 가져오기
    // 임시 데이터
    this.chatRooms = [];
    this.isLoading = false;
  }

  /**
   * 새 문의 시작
   */
  startNewChat(): void {
    this.router.navigate(['/chat-talk/client/chat'], {
      queryParams: {
        storeId: this.storeId,
        isNew: true
      }
    });
  }

  /**
   * 기존 채팅방 열기
   */
  openChatRoom(room: IChatRoom): void {
    this.router.navigate(['/chat-talk/client/chat'], {
      queryParams: {
        storeId: this.storeId,
        roomId: room.id
      }
    });
  }

  /**
   * 채팅방 상태 표시 텍스트
   */
  getRoomStatusText(status: RoomStatus): string {
    const statusMap = {
      [RoomStatus.BOT_ACTIVE]: '챗봇 응답',
      [RoomStatus.WAITING_AGENT]: '상담원 대기중',
      [RoomStatus.AGENT_ACTIVE]: '상담 진행중',
      [RoomStatus.RESOLVED]: '완료',
      [RoomStatus.CLOSED]: '종료'
    };
    return statusMap[status] || status;
  }

  /**
   * 마지막 메시지 시간 포맷
   */
  formatTime(date: Date): string {
    if (!date) return '';
    
    const now = new Date();
    const diff = now.getTime() - new Date(date).getTime();
    const minutes = Math.floor(diff / (1000 * 60));
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const days = Math.floor(diff / (1000 * 60 * 60 * 24));

    if (minutes < 1) return '방금 전';
    if (minutes < 60) return `${minutes}분 전`;
    if (hours < 24) return `${hours}시간 전`;
    if (days < 7) return `${days}일 전`;

    return new Date(date).toLocaleDateString();
  }
}
